@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gruppo&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #F4F4F4;
  color: #37474F;
}

main {
  max-width: 1024px;
  margin: 0 auto;
}

.pt-border {
  border: 2px solid #20abfe;
}

.rs-border {
  border: 2px solid #6bc424;
}

.pt-text {
  color: #20abfe;
}

.rs-text {
  color: #6bc424;
}

.unactive {
  opacity: 0.3;
  filter: grayscale(100%);
}

.filters {
  margin-top: 30px;
}
.filters .lists {
  display: flex;
  justify-content: space-around;
}
.filters .lists .list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.filters .lists .list label {
  margin-bottom: 5px;
  border-bottom: 2px solid #fe7320;
  width: 100%;
  text-align: center;
}
.filters .lists .list select {
  margin: 0 10px 10px 10px;
  scrollbar-width: none;
  -ms-overflow-style: none;
  min-width: 150px;
}
.filters .lists .list select option {
  margin: 1px 0;
  padding: 2px 5px;
}
.filters .lists .list select option:hover {
  background: rgba(254, 115, 32, 0.2196078431);
}
.filters .lists .list select option:checked {
  background: #fe7320;
  color: white;
}
.filters .lists .list select[multiple]:focus option:checked {
  background: #fe7320 linear-gradient(0deg, #fe7320 0%, #fe7320 100%);
}
.filters .lists .list select::-webkit-scrollbar {
  display: none;
}
.filters .salary {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.filters .salary .values {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: -15px;
}
.filters .salary .slider-container {
  display: flex;
}
.filters .salary .slider-container .checkbox-container {
  display: flex;
  align-items: center;
  margin-right: 10px;
  width: 90px;
}
.filters .salary .slider-container .container {
  width: 100%;
  position: relative;
  height: 50px;
}
.filters .salary .slider-container .container .slider-track {
  width: 100%;
  height: 2px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 5px;
}
.filters .salary .slider-container .container input[type=range] {
  appearance: none;
  width: 100%;
  outline: none;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background-color: transparent;
  pointer-events: none;
}
.filters .salary .slider-container .container input[type=range]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 5px;
}
.filters .salary .slider-container .container input[type=range]::-moz-range-track {
  -moz-appearance: none;
  height: 5px;
}
.filters .salary .slider-container .container input[type=range]::-ms-track {
  appearance: none;
  height: 5px;
}
.filters .salary .slider-container .container input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  background-color: #fe7320;
  border: solid 2px #fe7320;
  cursor: pointer;
  border-radius: 50%;
  pointer-events: auto;
}
.filters .salary .slider-container .container input[type=range]::-moz-range-thumb {
  -moz-appearance: none;
  height: 15px;
  width: 15px;
  outline: none;
  background-color: #fe7320;
  border: solid 2px #fe7320;
  cursor: pointer;
  border-radius: 50%;
  pointer-events: auto;
}
.filters .salary .slider-container .container input[type=range]::-ms-thumb {
  appearance: none;
  height: 15px;
  width: 15px;
  background-color: #fe7320;
  border: solid 2px #fe7320;
  cursor: pointer;
  border-radius: 50%;
  pointer-events: auto;
}
.filters .salary .slider-container .container input[type=range]:active::-webkit-slider-thumb {
  background-color: #FFFFFF;
  border: solid 2px #fe7320;
}
.filters .salary .slider-container .container input[type=range]:active::-moz-range-thumb {
  background-color: #FFFFFF;
  border: solid 2px #fe7320;
}
.filters .salary .slider-container .container input[type=range]:active::-ms-thumb {
  background-color: #FFFFFF;
  border: solid 2px #fe7320;
}
.filters .salary .slider-container .container input[type=range]:hover::-webkit-slider-thumb {
  outline: 10px solid rgba(254, 115, 32, 0.2196078431);
}
.filters .salary .slider-container .container input[type=range]:hover::-moz-range-thumb {
  outline: 10px solid rgba(254, 115, 32, 0.2196078431);
}
.filters .salary .slider-container .container input[type=range]:hover::-ms-thumb {
  outline: 10px solid rgba(254, 115, 32, 0.2196078431);
}
.filters .checkboxes {
  display: flex;
  margin-bottom: 30px;
  justify-content: center;
}
.filters .checkbox-container {
  margin: 10px 20px;
  display: block;
  width: 150px;
}
.filters .checkbox-container .checkbox {
  display: block;
  position: relative;
  height: 20px;
  width: 20px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Hide the browser's default checkbox */
  /* Create a custom checkbox */
  /* On mouse-over, add a grey background color */
  /* When the checkbox is checked, add a blue background */
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Show the checkmark when checked */
  /* Style the checkmark/indicator */
}
.filters .checkbox-container .checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
}
.filters .checkbox-container .checkbox .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  border: 1px #37474F solid;
  border-radius: 3px;
}
.filters .checkbox-container .checkbox:hover input ~ .checkmark {
  background-color: rgba(254, 115, 32, 0.2196078431);
}
.filters .checkbox-container .checkbox input:checked ~ .checkmark {
  background-color: #fe7320;
  border: 1px #fe7320 solid;
}
.filters .checkbox-container .checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.filters .checkbox-container .checkbox input:checked ~ .checkmark:after {
  display: block;
}
.filters .checkbox-container .checkbox .checkmark:after {
  left: 5px;
  top: 2px;
  width: 5px;
  height: 9px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(35deg);
  -ms-transform: rotate(35deg);
  transform: rotate(35deg);
}
.filters .checkbox-container .checkbox .label-text {
  position: relative;
  display: inline-block;
  left: 25px;
  font-size: 14px;
  width: max-content;
}
.filters .search {
  display: flex;
  justify-content: right;
  align-items: center;
  margin-bottom: 20px;
}
.filters .search .search-input {
  display: flex;
  width: 100%;
}
.filters .search .search-input input {
  background-color: white;
  width: 100%;
  height: 100%;
  padding: 10px 5px;
  border-radius: 5px;
  border: 1px solid #fe7320;
}
.filters .search .search-btn {
  padding: 10px 20px;
  color: white;
  background-color: #fe7320;
  border-radius: 5px;
  border: 2px solid #fe7320;
  cursor: pointer;
  margin: 0 20px;
}
.filters .search .search-btn:hover {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2), -2px -2px 4px rgba(0, 0, 0, 0.2);
}
.filters .search .search-btn:active {
  scale: 0.95;
}
.filters .info-bar {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  margin: 10px 0 20px 0;
}

.results {
  margin: 50px 25px;
}

.result {
  background-color: #FFFFFF;
  margin: 20px;
  padding: 20px;
  border-radius: 8px;
  border-top-right-radius: 15px;
  box-shadow: 0 0 25px 2px rgba(51, 51, 51, 0.2);
  position: relative;
}
.result .top {
  min-height: 50px;
  display: flex;
  justify-content: space-between;
}
.result .top .top-left {
  min-width: 100px;
  display: flex;
}
.result .top .top-left .lang {
  position: absolute;
  top: 3px;
  right: 3px;
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  background-color: rgba(55, 71, 79, 0.4823529412);
  margin: 5px;
  border-radius: 15px;
  color: #FFFFFF;
}
.result .top .top-left .lang p {
  height: 21px;
  opacity: 1;
  font-family: "Gruppo", cursive;
  text-transform: uppercase;
}
.result .top .top-left .branche-name {
  min-width: 40px;
  display: flex;
  flex-direction: column;
  color: #37474F;
}
.result .top .top-left .branche-name .branche {
  height: 50%;
  font-weight: 300;
  font-size: 14px;
  display: flex;
  justify-content: left;
  align-items: flex-end;
  line-height: 1.2em;
  padding: 0 5px;
}
.result .top .top-left .branche-name .name {
  display: flex;
  justify-content: left;
  align-items: flex-start;
  height: 50%;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2em;
  padding: 0 5px;
}
.result .top .top-right {
  min-width: 100px;
  display: flex;
  justify-content: left;
  align-items: center;
}
.result .top .top-right .jobtype {
  font-weight: 300;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1.2em;
  margin: 0 10px;
  color: black;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(34, 157, 251, 0.1215686275);
}
.result .top .top-right .salary {
  font-weight: 300;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1.2em;
  margin: 0 10px;
  color: black;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(34, 139, 34, 0.1843137255);
}
.result .bottom {
  min-height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.result .bottom .bottom-left {
  min-width: 100px;
  display: flex;
  justify-content: left;
  align-items: center;
}
.result .bottom .bottom-left .city {
  display: flex;
  align-items: center;
  font-weight: 300;
  padding: 0 5px;
  font-size: 14px;
  margin-right: 5px;
  color: black;
}
.result .bottom .bottom-left .city img {
  height: 14px;
  padding-right: 5px;
}
.result .bottom .bottom-left .remote {
  padding: 0 5px;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.2em;
  margin: 0 10px;
  color: black;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(251, 80, 180, 0.1294117647);
  display: flex;
  align-items: center;
}
.result .bottom .bottom-left .relocation {
  padding: 0 5px;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.2em;
  margin: 0 10px;
  color: black;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(255, 128, 0, 0.1411764706);
  display: flex;
  align-items: center;
}
.result .bottom .bottom-right {
  min-width: 100px;
  display: flex;
}
.result .bottom .bottom-right .employmentform {
  display: flex;
}
.result .bottom .bottom-right .employmentform p {
  padding: 0 5px;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.2em;
  margin: 0 10px;
  color: black;
  display: flex;
  align-items: center;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(119, 136, 153, 0.1921568627);
}

.no-results {
  display: none;
  z-index: -1;
}
.no-results.active {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  background: url("../img/frown.svg") no-repeat;
  background-position: center;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  background-size: contain;
}
.no-results img {
  width: 50%;
}